import { Grid, Skeleton, Stack } from '@mui/material';
import { useEffect, useState } from 'react';
import { NovedadesService } from "../../../Services/Comisiones/Home";
import { CNotificacion } from './CNotificaciones/CNotificacion';
import { CCard } from '../../../Components/CCard/CCard';
import { ModalRangos } from '../../../Components/Modal/ModalRangos';
import { IResponseRangoActual, IResponseRangos } from '../../../Services/Comisiones/Interfaces/IHome';
import CModalRangos from './CModalRangos/CModalRangos';

const LoadSection = () => {
    return (
        <>
            <Grid container item sm={4} sx={{ p: 1 }}  >
                <Grid container item lg={12} style={{ marginBottom: 25 }} >
                    <Stack spacing={1} sx={{ minWidth: '100%' }} style={{ alignItems: "center" }} >
                        <Skeleton variant="rounded" sx={{ width: '100%' }} height={350} />
                    </Stack>
                </Grid>
            </Grid>
            <Grid container item sm={4} sx={{ p: 1 }}  >
                <Grid container item lg={12} style={{ marginBottom: 25 }} >
                    <Stack spacing={1} sx={{ minWidth: '100%' }} style={{ alignItems: "center" }} >
                        <Skeleton variant="rounded" sx={{ width: '100%' }} height={350} />
                    </Stack>
                </Grid>
            </Grid>
            <Grid container item sm={4} sx={{ p: 1 }}  >
                <Grid container item lg={12} style={{ marginBottom: 25 }} >
                    <Stack spacing={1} sx={{ minWidth: '100%' }} style={{ alignItems: "center" }} >
                        <Skeleton variant="rounded" sx={{ width: '100%' }} height={350} />
                    </Stack>
                </Grid>
            </Grid>

        </>
    );
};

const MCard = () => {
    const [open, setOpen] = useState(false);
    const [rangoActual, setRangoActual] = useState<IResponseRangoActual>({
        descripcion: 'cargando...',
        imagen: '',
        subtitulo: 'cargando...',
        titulo: 'cargando...'
    });

    const handleClickOpen = (event: React.MouseEvent<HTMLElement, MouseEvent>) => {
        setOpen(true);
    };

    const handleClose = (value: IResponseRangos) => {
        setOpen(false);
        console.log(value)
    };

    const [carga, setCarga] = useState(true);
    const getTarjetas = async () => {
        try {
            const { data } = await NovedadesService();
            if (data.status == 1) {
                setRangoActual(data.data.rangoActual)
                setCarga(false);
            }
        } catch (error) {
            console.log(error)
        }
    };
    useEffect(() => {
        getTarjetas();
    }, []);

    return (
        <>
            {
                carga ? (<LoadSection />) :
                    (
                        <>
                            <Grid container item sm={4} sx={{ p: 1 }}  >
                                <Grid container item lg={12} style={{ marginBottom: 25 }} >
                                    <CNotificacion />
                                </Grid>
                            </Grid>
                            <Grid container item sm={4} sx={{ p: 1 }}  >
                                <Grid container item lg={12} style={{ marginBottom: 25 }} >
                                    <CCard
                                        descripcion={rangoActual.descripcion}
                                        imagen={`${process.env.REACT_APP_API_COMISIONES}${rangoActual.imagen}`}
                                        titulo={rangoActual.titulo}
                                        subtitulo={rangoActual.subtitulo}
                                        imagenWidth={150}
                                        href='/monte-sion'
                                        onClick={(e) => { handleClickOpen(e) }}
                                    >
                                        <CModalRangos open={open} onClose={handleClose}></CModalRangos>
                                    </CCard>
                                </Grid>
                            </Grid>
                            <Grid container item sm={4} sx={{ p: 1 }}  >
                                <Grid container item lg={12} style={{ marginBottom: 25 }} >
                                    <CCard
                                        descripcion='¡No te quedes fuera! Concreta tus ventas, se parte de los 30 mejores del Mes y gana el BONO de 300$'
                                        imagen={`${process.env.REACT_APP_API_COMISIONES}assest/top/top.png`}
                                        titulo='BONO MEJOR VENDEDOR'
                                        subtitulo='Desde el 1 de marzo de 2023 al 31 de marzo de 2023'
                                        href='#'
                                        onClick={() => { }}
                                    />
                                </Grid>
                            </Grid>
                        </>
                    )
            }
        </>
    );
}

export default MCard;