import { Backdrop, Card, CardContent, Chip, CircularProgress, createStyles, Grid, makeStyles, Theme, Typography } from '@mui/material';
import { AxiosError } from 'axios';
import { useEffect, useState } from "react";
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { CBreadcrumbs } from '../../Components/CBreadcrumbs';
import { setToken } from '../../Reducers/Slices/LoginSlice';
import { HomeService } from '../../Services/Comisiones/Home';
import DashboardIcon from '@mui/icons-material/Dashboard';
import ListCCard from './Components/ListCCard';
import MCard from './Components/MCard';
import { ErrorAxios } from '../../Utils/AxiosError';
import { CIncentivos } from './Components/CIncentivos/CIncentivos';


const Home = () => {
    const [cicloActual, setCicloActual] = useState<string>('');
    const [spinner, setSpinner] = useState(true);
    const nagivate = useNavigate();
    const dispatch = useDispatch();
    const updateToken = (token: boolean) => {
        dispatch(
            setToken({
                token: token
            })
        )
    }
    const homeApi = async () => {
        try {
            const { data, headers } = await HomeService();
            setCicloActual(data.data.fechaCiclo)
            setSpinner(false);
        } catch (error) {
            const err = error as AxiosError;
            let detectError = ErrorAxios(err.response?.status);
            updateToken(detectError.statusToken);
            nagivate(detectError.route);
        }
    }

    useEffect(() => {
        homeApi()
        return () => {
            //console.log('a la salida')
        }
    }, []);

    return (
        <>
            <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={spinner}>
                <CircularProgress color="inherit" />
            </Backdrop>
            <CBreadcrumbs icon={<DashboardIcon />} nombreRoute={"Tablero"} route={"/inicio"} nombresRoutes={[]} routes={[]}></CBreadcrumbs>
            <Typography variant="h5" component="h2" sx={{ pl: 1, pr: 1 }}>
                Bienvenido
            </Typography>
            <Chip color="info" label={`Analisis ${cicloActual}`} style={{ borderRadius: 5 }} sx={{ ml: 1 }} />
            <Chip color="success" label={`ACTIVO`} style={{ borderRadius: 5 }} sx={{ ml: 1 }} />
            <Grid container item xs={12} alignItems="center" justifyContent="center">
                <ListCCard previewLoad={4} />
            </Grid>
            <Grid container item xs={12} alignItems="center" justifyContent="center">
                <MCard />
            </Grid>
            <Grid container item xs={12} alignItems="center" justifyContent="center">
                <Grid container item sm={6} sx={{ p: 1 }}  >
                    <Grid container item lg={12} style={{ marginBottom: 25 }} >
                        {/* <CChartDias /> */}
                    </Grid>
                </Grid>
                <CIncentivos />
            </Grid>
        </>
    );
}

export default Home;