
import { Button, Card, CardActions, CardContent, CardMedia, ListItemButton, Typography } from '@mui/material'
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';
export interface CardDefaultProps {
    imagen: string;
    descripcion: string;
    subtitulo: string;
    router: string;
}
const initialState = {
    wrap: true,
    text: "Leer mas."
}

export const CardDefault = ({ imagen, descripcion, subtitulo, router }: CardDefaultProps) => {
    const nagivate = useNavigate();
    const [{ text, wrap }, setWrap] = useState(initialState)
    const onWrap = () => {
        if (wrap) {
            setWrap({
                wrap: false,
                text: 'Leer menos.'
            });
        }
        else {
            setWrap(initialState)
        }
    }
    const onClick = () => {
        if (router!="") {
            nagivate(router);
        }
    }
    return (
        <Card sx={{ minWidth: '100%' }} >
            <React.Fragment>
                <CardMedia
                    component="img"
                    alt={descripcion}
                    height="240"
                    image={`${process.env.REACT_APP_API_COMISIONES}${imagen}?w=248&fit=crop&auto=format`}
                    style={{ cursor: 'pointer' }}
                    onClick={onClick} />
                <CardContent>
                    <Typography gutterBottom variant="subtitle2" component="div">
                        {subtitulo}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" noWrap={wrap}>
                        {descripcion}
                    </Typography>
                    <Link to="#" style={{ fontSize: 13 }} onClick={onWrap} >{text}</Link>
                </CardContent>
                <CardActions>
                    <Button size="small" component={Link} to={router}>
                        Ver condiciones incentivos
                    </Button>
                </CardActions>
            </React.Fragment>
        </Card >
    );
}
