import { Skeleton, Stack } from "@mui/material";
import { CreateArray } from "../../Utils/others";

interface SkeletonLoadProps {
    previewLoad: number;
    height: number;
}
export const SkeletonLoad = ({ previewLoad, height }: SkeletonLoadProps) => {
    const loadInicial = CreateArray(previewLoad);
    return (
        <>
            {
                loadInicial.map((initial, i) => (
                    <Stack key={i} spacing={1} sx={{ minWidth: '100%' }} style={{ alignItems: "center" }} >
                        <Skeleton variant="rounded" sx={{ width: '100%' }} height={height} />
                    </Stack>
                ))
            }
        </>
    );
};