import { Grid, Skeleton, Stack } from "@mui/material";
import { useEffect, useState } from "react";
import { TarjetaService } from "../../../Services/Comisiones/Home";
import { ICCard } from "../../../Services/Comisiones/Interfaces/IHome";
import { CreateArray } from "../../../Utils/others";
import CCardResumen from "../../../Components/CCardResumen/CCardResumen";
import { initialDataFakeCards } from "../../../DataFake/DataFake";

interface ListCCardProps {
    previewLoad: number;
}

const ListCCard = ({ previewLoad }: ListCCardProps) => {
    const [tarjetas, setTarjetas] = useState<ICCard[]>(initialDataFakeCards);
    const [carga, setCarga] = useState(true);
    const getTarjetas = async () => {
        try {
            const { data } = await TarjetaService();
            if (data.status == 1) {
                setTarjetas(data.data);
                setCarga(false);
            }
            else {

            }
        } catch (error) {
            console.log(error);
            setCarga(false);
        }
    };
    useEffect(() => {
        getTarjetas();
    }, []);
    return (
        <>
            {tarjetas.map((tarjeta, i) => {
                return (<CCardResumen
                    key={i}
                    icon={tarjeta.icon}
                    color={tarjeta.color}
                    descripcion={tarjeta.titulo}
                    monto={tarjeta.valor}
                    route={tarjeta.routeApp}
                    textToolip={tarjeta.titulo}
                    estado={carga}
                />)
            })}
        </>
    );
}

export default ListCCard;