import axios from 'axios';
import { AxiosRequest,AxiosResponse } from '../../Utils/Axios';
import { IResponseCard, IResponseHome, IResponseNovedades, IResponseTodosRangos } from './Interfaces/IHome';
//llamada a interceptor
AxiosRequest();
AxiosResponse();
export async function TarjetaService() {
    return await axios.get<IResponseCard>(`${process.env.REACT_APP_API_COMISIONES}api/home/tarjeta`);
}
export async function NovedadesService() {
    return await axios.get<IResponseNovedades>(`${process.env.REACT_APP_API_COMISIONES}api/home/novedades`);
}
export async function HomeService() {
    return await axios.get<IResponseHome>(`${process.env.REACT_APP_API_COMISIONES}api/home`);
}
export async function TodosRangoService() {
    return await axios.get<IResponseTodosRangos>(`${process.env.REACT_APP_API_COMISIONES}api/home/todos-rangos`);
}