import axios from 'axios';
import { AxiosRequest, AxiosResponse } from '../../Utils/Axios';
import { IResponseChartProgresoIncentivo, IResponseIncentivoIndex, IResponseIncentivoLista } from './Interfaces/IIncentivos';
//llamada a interceptor
AxiosRequest();
AxiosResponse();
export async function IncentivoIndexService() {
    return await axios.get<IResponseIncentivoIndex>(`${process.env.REACT_APP_API_COMISIONES}api/incentivo`);
}
export async function ListaIncentivosService() {
    return await axios.get<IResponseIncentivoLista>(`${process.env.REACT_APP_API_COMISIONES}api/incentivo/list`);
}
export async function IncentivosProgresoService() {
    return await axios.get<IResponseChartProgresoIncentivo>(`${process.env.REACT_APP_API_COMISIONES}api/incentivo/chart-gauge`);
}

