import React, { useEffect, useState } from 'react'
import { ModalRangos } from '../../../../Components/Modal/ModalRangos'
import { IResponseRangos } from '../../../../Services/Comisiones/Interfaces/IHome';
import { TodosRangoService } from '../../../../Services/Comisiones/Home';

interface CModalRangos {
    open: boolean;
    onClose: (value: IResponseRangos) => void;
}
const CModalRangos = ({ open, onClose }: CModalRangos) => {
    //const [open, setOpen] = useState(false);
    const [rangos, setRangos] = useState<IResponseRangos[]>([]);
    const [carga, setCarga] = useState(true);
    const getTarjetas = async () => {
        try {
            const { data } = await TodosRangoService();
            if (data.status == 1) {
                setRangos(data.data.todosRangos);
                setCarga(false);
            }
        } catch (error) {
            console.log(error)
        }
    };
    const [selectedValue, setSelectedValue] = useState<IResponseRangos>({
        descripcion: '',
        enUso: false,
        imagen: '',
        subtitulo: '',
        titulo: ''
    });
    useEffect(() => {
        getTarjetas();
    }, []);
    return (
        <>
            <ModalRangos
                listRangos={rangos}
                selectedValue={selectedValue}
                open={open}
                onClose={(e) => { onClose(e) }}
            />
        </>
    )
}

export default CModalRangos
