import { Box, Card, CardActionArea, CardContent, CardMedia, Tooltip, Typography } from '@mui/material'

interface CardDetailProps {
    imagen: string;
    descripcion: string;
    rango: string;
    detalle: string;
    color?: boolean;
    onClick: React.MouseEventHandler<HTMLSpanElement>
}
export const CardDetail = ({ imagen, descripcion, detalle, rango, color, onClick }: CardDetailProps) => {
    return (
        <Card sx={{ minWidth: '100%' }} style={{ alignItems: "center", background: (color ? 'linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 12%, rgba(0,212,255,1) 100%)' : '#FFFFFF') }} >
            <CardContent sx={{ display: 'flex' }} style={{ padding: 0 }}>
                <CardMedia
                    component="img"
                    sx={{ width: 96, alignItems: "center" }}
                    image={`${process.env.REACT_APP_API_COMISIONES}${imagen}`}
                    alt={rango}
                    loading="lazy"
                />
                <Box style={{ width: '100%' }}>
                    <CardContent style={{ padding: 10 }}>
                        <Typography
                            variant="body2" sx={{ m: 0 }}
                            align={'center'}
                        >
                            {descripcion}
                        </Typography>
                        <Typography
                            variant="body2"
                            sx={{
                                m: 0,
                                "&:hover": {
                                    background: "linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(197,197,197,1) 100%)",
                                }
                            }}
                            align={'center'}
                            style={{ fontWeight: 'bold', cursor: 'pointer' }}
                            onClick={onClick}
                        >
                            {rango}
                        </Typography>
                        <hr style={{ margin: 0, textAlign: 'center' }} />
                        <Typography component="div" variant="body2" align={'center'}>
                            {detalle}
                        </Typography>
                    </CardContent>
                </Box>
            </CardContent>
        </Card>
    )
}