import { Grid, Skeleton, Stack } from '@mui/material'
import { AxiosError } from 'axios';
import { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { CardDefault } from '../../../../Components/CardDefault/CardDefault';
import { SkeletonLoad } from '../../../../Components/Skeleton/Skeleton';
import { setToken } from '../../../../Reducers/Slices/LoginSlice';
import { ListaIncentivosService } from '../../../../Services/Comisiones/Incentivos';
import { IListaIncentivo } from '../../../../Services/Comisiones/Interfaces/IIncentivos';
import { ErrorAxios } from '../../../../Utils/AxiosError';

const initalState: IListaIncentivo = {
    descripcion: '',
    imagen: '',
    titulo: ''
};
export const CIncentivos = () => {
    const [{ descripcion, imagen, titulo }, setIncentivo] = useState(initalState);
    const nagivate = useNavigate();
    const dispatch = useDispatch();
    const updateToken = (token: boolean) => {
        dispatch(
            setToken({
                token: token
            })
        )
    }
    const [carga, setCarga] = useState(true);
    const listIncentivoApi = async () => {
        try {
            const { data, headers } = await ListaIncentivosService()
            setIncentivo(data.data[0]);
            setCarga(false);
        } catch (error) {
            const err = error as AxiosError;
            let detectError = ErrorAxios(err.response?.status);
            updateToken(detectError.statusToken);
            nagivate(detectError.route);
            setCarga(false);
        }
    }
    useEffect(() => {
        listIncentivoApi();

        return () => {

        }
    }, [setIncentivo])

    return (
        <Grid container item sm={6} sx={{ p: 1 }}  >
            {
                carga ? (<SkeletonLoad previewLoad={1} height={350}/>) : (
                    <CardDefault descripcion={descripcion} imagen={imagen} subtitulo={titulo} router={"/incentivos"} />
                )
            }
        </Grid>
    )
}
