import { Box, Button, Card, CardActionArea, CardActions, CardContent, CardMedia, Tooltip, Typography } from '@mui/material'
import React, { useState } from 'react'
import { Link } from 'react-router-dom';
interface CCardProps {
    titulo: string;
    imagen: string;
    descripcion: string;
    subtitulo: string;
    imagenWidth?: number;
    href: string;
    children?: JSX.Element
    onClick: (event: React.MouseEvent<HTMLElement>) => void;
}
const initialState = {
    wrap: true,
    text: "Leer mas."
}
export const CCard = ({ titulo, subtitulo, descripcion, imagen, imagenWidth, children, onClick, href }: CCardProps) => {


    const [{ text, wrap }, setWrap] = useState(initialState)
    const onWrap = () => {
        if (wrap) {
            setWrap({
                wrap: false,
                text: 'Leer menos.'
            });
        }
        else {
            setWrap(initialState)
        }
    }
    return (
        <>
            <Card sx={{ minWidth: '100%' }}>
                <React.Fragment>
                    <CardContent>
                        <Box alignItems="center" >
                            <Tooltip title={'Detalle'} arrow followCursor>
                                <CardActionArea>
                                    <Typography component="div" variant="subtitle2" align="center">
                                        {titulo}
                                    </Typography>
                                </CardActionArea>
                            </Tooltip>
                            <Tooltip title={'Click para ver mas detalle'} arrow followCursor>
                                <CardActionArea onClick={onClick}>
                                    <div style={{ justifyContent: "center", display: "flex", padding: '5px' }} >
                                        <CardMedia
                                            component="img"
                                            sx={{ width: imagenWidth ? imagenWidth : 100, alignItems: "center" }}
                                            image={imagen}
                                            alt={descripcion}
                                        />
                                    </div>
                                </CardActionArea>
                            </Tooltip>
                            <Typography component="div" variant="subtitle2" align="center">
                                {subtitulo}
                            </Typography>
                            <Typography variant="body2" color="text.secondary" component="div" align="center" noWrap={wrap}>
                                {descripcion}
                            </Typography>
                            <Link to="#" style={{ fontSize: 13 }} onClick={onWrap} >{text}</Link>
                        </Box>
                    </CardContent>
                    <CardActions>
                        <Button size="small" component={Link} to={href}>Mas informacion ...</Button>
                    </CardActions>
                </React.Fragment>
            </Card>
            {children}
        </>
    )
}
