import * as React from 'react';
import Button from '@mui/material/Button';
import Avatar from '@mui/material/Avatar';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog, { DialogProps } from '@mui/material/Dialog';

import { CardDetail } from '../CardDetail/CardDetail';
import { DialogContent, Typography } from '@mui/material';
import { IResponseRangos } from '../../Services/Comisiones/Interfaces/IHome';
import { useNavigate } from "react-router-dom";

export interface SimpleDialogProps {
    open: boolean;
    selectedValue: IResponseRangos;
    onClose: (value: IResponseRangos) => void;
    listRangos: IResponseRangos[]
}

export const ModalRangos = (props: SimpleDialogProps) => {
    const navigate = useNavigate();

    const [scroll, setScroll] = React.useState<DialogProps['scroll']>('paper');
    const { onClose, selectedValue, open } = props;

    const handleClose = () => {
        onClose(selectedValue);
    };

    const handleListItemClick = (value: IResponseRangos) => {
        onClose(value);
    };

    return (
        <Dialog onClose={handleClose}
            open={open}
            scroll={scroll}
            aria-labelledby="scroll-dialog-title"
            aria-describedby="scroll-dialog-description"
        >
            <DialogTitle component="div" variant="subtitle1" align="center">
                Todos los rangos
                <Typography variant='subtitle2' >Comisiones de cuerdo a las ventas registradas durante el mes en monte Sion</Typography>
            </DialogTitle>

            <DialogContent>
                <List>
                    {props.listRangos.map((rango, i) => (
                        <ListItem disableGutters key={i} >
                            <ListItemButton
                                onClick={() => {
                                    rango.enUso ? navigate('/monte-sion') : handleListItemClick(rango)
                                }}
                                key={i}>
                                <CardDetail
                                    rango={rango.subtitulo}
                                    descripcion={rango.titulo}
                                    detalle={rango.descripcion}
                                    imagen={rango.imagen}
                                    color={rango.enUso}
                                    onClick={()=>{}}
                                />
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>
            </DialogContent>
        </Dialog>
    );
}